const innerAudioContext = uni.createInnerAudioContext();
export default {
  data() {
    return {
      show: false,
      src: 'http://pic2.sc.chinaz.com/Files/pic/pic9/202002/hpic2119_s.jpg',
      background: {
        backgroundImage: 'linear-gradient(-225deg, #69EACB 0%, #EACCF8 48%, #6654F1 100%)'
      },
      pic_url: '',
      music_url: '',
      music_name: '',
      music_anthor: '',
      music: {
        startTime: '',
        endTime: ''
      },
      progress:0,
      player:false,
      NAVBAR_HEIGHT:44
    };
  },
  onLoad() {
    innerAudioContext.autoplay = true;
    this.getMusicInfo()
    innerAudioContext.onTimeUpdate((e)=>{
      // this.player = true
      this.music.startTime = this.formatTime(innerAudioContext.currentTime)
      this.progress = parseInt(innerAudioContext.currentTime/innerAudioContext.duration * 100)
    })
    innerAudioContext.onCanplay((e)=>{
      this.player = true
    })
    innerAudioContext.onError((e)=>{
      this.player = false
    })
  },
  mounted() {
    console.log(this.$refs.navbar)
  },
  methods: {
    getMusicInfo(){
      this.$u.get('https://api.uomg.com/api/rand.music?', {
        format: 'json',
        sort: '热歌榜'
      })
      .then(res => {
        console.log(res);
        this.pic_url = res.data.picurl;
        this.music_url = res.data.url;
        this.music_name = res.data.name;
        this.music_anthor = res.data.artistsname;
        innerAudioContext.src = res.data.url;
        setTimeout(() => {
          this.music.startTime = this.formatTime(innerAudioContext.currentTime) 
          this.music.endTime = this.formatTime(innerAudioContext.duration);
        }, 500);
      });
    },
    onUserMenu() {
      this.show = true;
    },
    onPlay() {
      console.log('播放');
      // innerAudioContext.src = this.music_url;
      innerAudioContext.play()
      this.player = true
    },
    onPause(){
      console.log('暂停')
      innerAudioContext.pause();
      this.player = false
    },
    onStop(){
      console.log('stop')
      innerAudioContext.stop()
    },
    formatTime(timer) {
      if (timer === 0 || typeof timer !== 'number') {
        return '00:00';
      }
      let mm = Math.floor(timer / 60);
      let ss = Math.floor(timer % 60);
      if (mm < 10) {
        mm = '0' + mm;
      }
      if (ss < 10) {
        ss = '0' + ss;
      }
      return mm + ':' + ss;
    }
  }
};
